<template>
  <div class="myset">
    <v-header>设置</v-header>
    <ul>
      <li @click="goMyData()">个人资料</li>
      <li @click="goRevisePwd()">修改密码</li>
    </ul>
  </div>
</template>
<script>
  export default {
    methods: {
      goMyData() {
        this.$router.push({ name: 'editinfo' });
      },
      goRevisePwd() {
        this.$router.push({ name: 'revisepwd' });
      }
    }
  };
</script>
<style lang="less" scoped>
  .myset {
    ul {
      margin-top: 96px;
      background: #ffffff;
      padding: 0 24px;
      li {
        width: '100%';
        height: 96px;
        line-height: 96px;
        color: #666666;
        font-size: 30px;
        border-bottom: 2px solid #f0f0f0;
      }
      li:last-child {
        border-bottom: none;
      }
    }
  }
</style>




